//  ~~~~~~ 选择框, 根据实际添加 ~~~~~~
const SelectChannels = ({ value = '', onChange = e => console.log(e) }) => {
    const [channelOptions, setChannelOptions] = useState([]);
    useEffect(() => {
      channelsRequestGet()
        .then(respObj => {
          const channelsList = respObj.results;
          let updateOptions = [];
          for (let index = 0; index < channelsList.length; index++) {
            const element = channelsList[index];
            updateOptions.push({
              value: element.id,
              label: element.name
            })
          }
          setChannelOptions(updateOptions);
        })
        .catch(error => {
          console.log(error);
          setChannelOptions([]);
        });
    }, []);
    return (<CustomSelect options={channelOptions} value={value} onChange={onChange}></CustomSelect>)
  };
  const SelectCategories = ({ value = '', onChange = e => console.log(e) }) => {
    const [options, setOptions] = useState([]);
    useEffect(() => {
      categoryRequestGet()
        .then(respObj => {
          const channelsList = respObj.results;
          let updateOptions = [];
          for (let index = 0; index < channelsList.length; index++) {
            const element = channelsList[index];
            updateOptions.push({
              value: element.id,
              label: element.category_name
            })
          }
          setOptions(updateOptions);
        })
        .catch(error => {
          console.log(error);
          setOptions([]);
        });
    }, []);
    return (<CustomSelect options={options} value={value} onChange={onChange}></CustomSelect>)
  };
  const SelectHasDown = (<CustomSelect options={[{ value: 0, label: '定制' }, { value: 1, label: '现货' }]}></CustomSelect>);
  const SelectBoolean = (<CustomSelect options={[{ value: 0, label: '否' }, { value: 1, label: '是' }]}></CustomSelect>);
  const formKeySelectComponentsObj = {
    // exampleColumn: SelectExample,
    // 'end_time': SelectEndTime,
    'channel_id': <SelectChannels />,
    'categories': <SelectCategories />,
    'has_done': SelectHasDown,
    'has_gallery' : SelectBoolean,
    'is_delete': SelectBoolean,
    'is_index': SelectBoolean,
    'is_new': SelectBoolean,
    'is_on_sale': SelectBoolean,
  };

