const actionButtons = (item, setVisible, setSelectedObj, selectButtonOnClick) => {
    const onClickInfo = () => {
        setSelectedObj(item);
        setVisible(2);
    };
    const gotoColorPricePage = (item) => {
        // 跳转到 颜色价格页面 传商品id
        navigate("/wxcolorprice", { state: { goodsobj: item } });
    }
    const btnList = [
        <Button onClick={onClickInfo} type="link">详情</Button>,
        <Button onClick={() => gotoColorPricePage(item)} type="link">颜色价格</Button>,
    ];
    if (selectButtonOnClick) {
        btnList.push(<Button onClick={() => selectButtonOnClick(item)} type="link">选择</Button>)
    }
    return btnList;
}
// 修改 ~~~~~~~ item keys ~~~~~~;
const renderItem = (item, setSelectedObj, selectButtonOnClick, setVisible, visible) => {
    // 必须 每一行渲染 的组件, 修改 ~~~~~~~ item keys ~~~~~~;
    const textsContent = (
        <div>
            <p>{`已上架: ${item.is_on_sale}`}</p>
            <p>{`总销量: ${item.sell_volume}`}</p>
            <p>{`说明: ${item.explains}`}</p>
            <p>{`上架时间: ${moment(item.add_time).format('YYYY-MM-DD HH:mm:ss')}`}</p>
            <p>{`更新时间: ${moment(item.update_time).format('YYYY-MM-DD HH:mm:ss')}`}</p>
        </div>
    )
    const updateItemObj = {
        id: item.id,
        actions: actionButtons(item, setVisible, setSelectedObj, selectButtonOnClick),
        img: item.home_pic1_url,
        avatar: '',
        title: item.name,
        description: `商品简介: ${item.goods_brief}`,
        content: textsContent,
    };
    return customRanderItem(updateItemObj, 120);
};

const judgeHasMore = (resp) => {
    if (Object.keys(resp).includes('next')
        && typeof resp.next === 'string'
        && resp.next.length > 5) {
        return true;
    }
    return false;
};

