import React from 'react';
import { Button } from 'antd';
import { DownOutlined } from '@ant-design/icons';

const toSourceScrollTop = (sourceScrollTop, clientHeight) => {
    // 数据加载，滚动条回到原来位置
    if (typeof document !== 'undefined') {
        // const sourceScrollTop = document.documentElement.scrollTop || document.body.scrollTop; //滚动的高度;
        // const clientHeight = document.documentElement.clientHeight || document.body.clientHeight; // 浏览器视口的高度;

        const toSourceScrollTopHandle = () => {
            const currentScrollTop = document.documentElement.scrollTop || document.body.scrollTop; //滚动的高度;

            if (sourceScrollTop + clientHeight < currentScrollTop) {
                if (document.documentElement.scrollTop) {
                    document.documentElement.scrollTo(0, sourceScrollTop);
                    document.removeEventListener('scroll', toSourceScrollTopHandle, false);
                }else if(document.body.scrollTop){
                    document.body.scrollTo(0, sourceScrollTop);
                    document.removeEventListener('scroll', toSourceScrollTopHandle, false);
                }
                console.log(currentScrollTop, currentScrollTop - clientHeight);
            }
        }
        document.addEventListener('scroll', toSourceScrollTopHandle, false);
    }
}

const LoadMoreButton = ({hasMore, onClick, loading}) => {
    // 读取更多按钮

    const loadMoreHandle = () => {
        if (typeof document !== 'undefined') {
            const sourceScrollTop = document.documentElement.scrollTop || document.body.scrollTop; //滚动的高度;
            const clientHeight = document.documentElement.clientHeight || document.body.clientHeight; // 浏览器视口的高度;
            onClick();
            toSourceScrollTop(sourceScrollTop, clientHeight);
        }else {
            onClick();
        }
    }

    const loadingOrNoMoreTips = () => {
        const tips = loading ? '加载中...' : '没有更多了...';
        return (<p>{tips}</p>);
    }
    return (
        <div style={{width: '100%', height: 'auto', margin: '1rem 1rem 1rem 0.1rem', textAlign: 'center'}}>
            {hasMore 
                ? (<Button
                        type="default"
                        shape="round"
                        icon={<DownOutlined />}
                        style={{width: '80%'}}
                        onClick={loadMoreHandle}
                        size="large"
                    >更多结果</Button>)
                : loadingOrNoMoreTips()}
        </div>
    )
}

LoadMoreButton.defaultProps = {
    hasMore: false,
    onClick: () => console.log('Load more button'),
    loading: false,
}

export { LoadMoreButton };
